<template>
  <div class="moviedetails">
    <div v-for="(item, index) in allmessge" :key="index">
      <div class="top">
        <a @click="goBack">
          <i></i>
        </a>
        <h3>{{item.nm}}</h3>
      </div>
      <a @click="$router.push({ path: '/movies', query: { plan: id }})">
        <div class="heade">
          <img :src="item.img" alt>
        </div>
        <ul class="count">
          <li>
            <img :src="item.img">
            <div>
              <h3>{{item.nm}}</h3>
              <p>{{item.enm}}</p>
              <p>
                <label>{{item.sc}}</label>
                ({{item.snum}})万人评
              </p>
              <p>
                {{item.cat}}
                <i></i>
              </p>
              <p>{{item.dir}}</p>
              <p>{{item.fra}}/{{item.episodeDur}}分钟</p>
              <p>{{item.pubDesc}}</p>
            </div>
            <img
              src="data:image/png;base64,"
            >
          </li>
        </ul>
      </a>
    </div>
    <div class="parent-dom">
      <vue-scroll>
        <ul class="child-dom time">
          <li v-for="(item, index) in dates" :key="index"  @click="toggle(index)"  :class="{'active':index ==checkindex }">{{item.date}}</li>
        </ul>
      </vue-scroll>
    </div>

    <ul class="active">
      <li @click="show">
        全城
        <i></i>
        <AllCity v-if="index == indexcurr"></AllCity>
      </li>
      <li @click="show">
        品牌
        <i></i>
        <Make v-if="index == indexcurrs"></Make>
         
      </li>
      <li @click="show">
        特色
        <i></i>
        <Service v-if="index == indexcurrss"></Service>
      </li>
    </ul>
    <div class="bottom">
      <ul>
        <li v-for="(item, index) in cinemas" :key="index">
          <div class="name">
            <h3>
              {{item.nm}}
              <i>{{item.sellPrice}}元起</i>
            </h3>
            <div class="addr">
              <p>{{item.addr}}</p>
            </div>
            <p><label>小吃</label><label>折扣卡</label></p>
            <p>开卡特惠，首单2张立减2元</p>
          </div>
          <label>{{item.distance}}</label>
        </li>
      </ul>
    </div>
   
    
    
    <router-view></router-view>
  </div>
</template>
<script>
import AllCity from "../components/AllCity.vue";
import Make from "../components/Make.vue";
import Service from "../components/Service.vue";
export default {
  name: "movieDetails",
  data() {
    return {
      id: null,
      allmessge: [],
      index: 3,
      indexcurr: 0,
      indexcurrs: 1,
      indexcurrss: 2,
      flag: true,
      dates: null,
      cinemas: null,
      dates: null,
      cinemas: null,
      checkindex:0
    };
  },
  components: {
    Make,
    Service,
    AllCity
  },
  created() {
    this.id = this.$route.params.id;
    var _this = this;
    this.$axios
      .get("http://api.kele8.cn/agent/http://m.maoyan.com/ajax/detailmovie?", {
        params: {
          movieId: _this.id
        }
      })
      .then(function(result) {
        // console.log(_this);
        // console.log(result.data);
        _this.allmessge.push(result.data.detailMovie);

        _this.allmessge[0].img =
          "http://p0.meituan.net/148.208/movie/" +
          _this.allmessge[0].img.split("/")[5];
        _this.allmessge[0].snum = (_this.allmessge[0].snum / 10000).toFixed(1);
        if (_this.id == "248172") {
          _this.allmessge[0].img =
            "https://p0.meituan.net/128.180/moviemachine/f7d2ad70eb79d6d9b8a197713db9b8c41711752.jpg";
        }
      });
    console.log(this.allmessge);
    this.$axios.get("/particulars.json").then(result => {
      console.log(result);
      this.dates = result.data.showDays.dates;
      this.cinemas = result.data.cinemas;
      console.log(this.dates);
    });
    console.log(this.id);
  },
  methods: {
    goBack: function() {
      this.$router.go(-1);
    },
    go() {
      this.$router.push({ path: "Movies", query: { plan: "private" } });
    },
    show(e) {
      if (this.flag) {
        $(e.target)
          .addClass("action")
          .siblings()
          .removeClass("action");
        this.index = $(e.target).index();
        this.flag = false;
      } else {
        this.index = 3;
        this.flag = true;
        $(e.target)
          .removeClass("action")
          .siblings()
          .removeClass("action");
      }
    },
    toggle(index){
      this.checkindex = index;
    }
  }
};
</script>
<style lang="less" scoped>
.moviedetails {
  height: 250px;
  .top {
    height: 50px;
    background-color: #e54847;
    // text-align: center;
    display: flex;
    align-items: center;
    a {
      flex-grow: 1;
      i {
        margin-left: 20px;
        display: inline-block;
        line-height: 50px;
        width: 16px;
        height: 16px;
        border-bottom: 2px solid #ffffff;
        border-left: 2px solid #ffffff;
        transform: rotate(45deg);
      }
    }
    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: normal;
      color: white;
      line-height: 50px;
      flex-grow: 1;
    }
  }
  .heade {
    overflow: hidden;
    background-color: #333;
    width: 100%;
    height: 200px;
    position: absolute;
    z-index: -1;
    img {
      width: 100%;
      height: 200px;

      opacity: 0.55;

      filter: blur(1.2rem);
      overflow: hidden;
    }
  }
  .count {
    height: 200px;
    list-style: none;
    margin-top: 5px;
    li {
      height: 200px;
      width: 100%;
      img:nth-of-type(1) {
        width: 110px;
        height: 162px;
        float: left;
      }
      div {
        float: left;
        margin-left: 10px;
      }
      img:nth-of-type(2) {
        width: 10px;
        height: 14px;
        margin-top: 80px;
      }
      h3 {
        margin: 0;
        color: white;
        font-size: 18px;
      }
      p:nth-of-type(2) label {
        color: #faaf00;
        font-weight: 700;
        font-size: 15px;
        margin-right: 5px;
      }
      p:nth-of-type(3) i {
        display: inline-block;
        width: 42px;
        height: 13px;
        background-image: url(data:image/png;base64,);
        background-size: 100% 100%;
      }
      p {
        margin: 0;
        color: white;
        font-size: 12px;
        margin-top: 5px;
        opacity: 0.8;
      }
    }
  }
  .active {
    position: relative;
    margin: 0;
    list-style: none;
    padding: 0;
    display: flex;
    height: 50px;
    align-items: center;
    li:nth-of-type(3) {
      &::after {
        border: none;
      }
    }
    li {
      // border-right: 1px solid #333;
      color: #777;
      font-size: 15px;
      flex-grow: 1;
      text-align: center;
      line-height: 50px;
      &::after {
        content: "";
        height: 20px;
        display: inline-block;
        border-left: 1px solid #e8e8e8;
        margin-left: 20px;
      }
      i {
        display: inline-block;
        border: 5px solid #b0b0b0;
        border-left-color: transparent;
        border-right-color: transparent;
        border-bottom-color: transparent;
        margin-left: 4px;
        margin-top: 5px;
      }
    }
    .action {
      color: #e54847;
    }
  }
  .bottom{
ul{
  list-style: none;
  padding:0;
  margin: 15px;
  li{
    margin-top: 20px;
    display:flex;
    justify-content: space-between;
    .name{
        h3{
          margin: 0;
          font-size: 15px;
          i{
            color: #e54847;
          }
        }
        .addr{
          width: 250px;
          overflow: hidden;
          p{
            width: 400px;
            margin: 0;
          }
        }
        p{
          margin: 5px;
          color: #777;
          font-size: 13px;
          label{
            border:1px solid #f90;
            color:#f90;
            margin-right: 5px; 
          }
        }
      }
      label{
        color: #777;
        font-size: 13px;
      }
  }
}
  }
  .time {
    list-style: none;
    display: flex;
    text-align: center;
    margin: 0;
    padding: 0;
    li {
      width: 100px;
      line-height: 30px;
      color: #777;
      margin-left: 10px;
      
    }
    .active {
      border-bottom: 1px solid #e54847;
      margin-top: -10px;
    }
  }
}
</style>
